.blogs {
    padding: 42px 0;

    @include media-breakpoint-down(xxlg) {
        padding: 30px 0;
    }

    @include media-breakpoint-down(md) {
        padding: 32px 0;
    }

    &__img {
        max-width: 936px;
        width: 100%;
        height: 525px;
        border-radius: 24px;
        overflow: hidden;

        @include media-breakpoint-down(lg) {
            height: 303px;
            border-radius: 0;
        }

        @include media-breakpoint-down(md) {
            height: 203px;
            border-radius: 0;
        }

        img {
            object-fit: cover;
        }
    }

    &__head {
        margin-top: 56px;
        padding-bottom: 32px;
        border-bottom: 1px solid rgba($color: #fff, $alpha: .1);

        @include media-breakpoint-down(lg) {

            margin-top: 16px;
            padding-bottom: 10px;
        }

        h2 {
            font-weight: 600;
            font-size: 30px;
            line-height: 46px;
            color: #FFFFFF;
            margin-bottom: 24px;

            @include media-breakpoint-down(lg) {
                font-size: 16px;
                line-height: 24px;
            }

        }




    }

    &__details {
        margin-top: 40px;

        @include media-breakpoint-down(lg) {
            margin-top: 24px;
        }

        li {
            margin-bottom: 32px;

            @include media-breakpoint-down(lg) {
                margin-bottom: 16px;
            }

            p {
                font-family: $popin;
                font-weight: 400;
                font-size: 16px;
                line-height: 32px;
                color: $white;
                opacity: .8;

                @include media-breakpoint-down(lg) {
                    font-size: 13px;
                    line-height: 22px;
                    color: #FFFFFF;
                    opacity: 0.7;
                }

            }
        }
    }

}

.blog-listing {
    background-image: url("../img/home/BG.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    &__wrp {
        margin-top: 72px;

        @include media-breakpoint-down(lg) {
            margin-top: 40px;
        }

    }
}

.blogCard {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;

    @include media-breakpoint-down(lg) {
        margin-bottom: 32px;
    }

    &__img {
        height: 211px;
        border-radius: 8px;
        width: 100%;
        object-fit: cover;
        object-position: top;

        @include media-breakpoint-down(lg) {
            height: 187px;

        }

    }

    &__body {
        margin-top: 24px;
        display: flex;
        flex-direction: column;
        flex: 1;

        &__desc {
            font-size: 18px;
            line-height: 26px;
            color: #FFFFFF;
            margin-bottom: 12px;
            flex: 1;

            @include media-breakpoint-down(lg) {
                font-size: 16px;
                line-height: 24px;
            }
        }
    }
}