.h1Heading,
.pDesc {
    text-align: center;
    margin: 0 auto;
}

.h1Heading {
    @extend .h1;
    text-transform: uppercase;
    color: $white;
}

.pDesc {
    @extend .fnt-24;
    color: $white;
}

.brd-btn {
    border-bottom: 1px solid $white;
}