body {
    font-family: 'Sora', sans-serif;
    // font-weight: $fnt-300;
    font-weight: 300;
    overflow-x: hidden;
    color: $primary;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
    background: #141518;

    // background-color: $bg-pri;
    p {
        font-size: rem(13px);
        line-height: rem(22px);
        margin: 0;
        padding: 0;
        font-family: $popin;
        font-weight: 400;

        @include media-breakpoint-up(lg) {
            font-size: rem(16px);
            line-height: rem(26px);
        }

        @include media-breakpoint-up(xxlg) {
            font-size: 16px;
            line-height: 26px;
        }
    }
}

.container {
    @include media-breakpoint-up(xxlg) {
        max-width: 1520px;
    }
}


//color
.clr-w {
    color: $white;
}

.clr-b {
    color: $black;
}

.clr-11 {
    color: #111317;
}

.clr-1a {
    color: #1A1A1A;
}

.clr-1b {
    color: #1B1E24;
}

.clr-4e {
    color: #4E617A;
}

.clr-ab {
    color: #AB1A39;
}

.clr-42 {
    color: #425466;
}


.fnt-500 {
    font-weight: 500;
}

.fnt-600 {
    font-weight: 600 !important;
}

.fnt-700 {
    font-weight: 700 !important;
}

.sec-100 {
    padding: 100px 0;
}

.h2 {
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;


    @include media-breakpoint-down(md) {
        font-size: 22px;
        line-height: 32px;
    }
}

.h3 {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;

    @include media-breakpoint-down(md) {
        font-size: 18px;
        line-height: 23px;
    }

}

.h4 {
    font-weight: 500;
    font-size: 22px !important;
    line-height: 30px !important;

    @include media-breakpoint-up(md) {
        font-size: 24px !important;
        line-height: 32px !important;
    }
}

.fnt-14 {
    font-size: 15px !important;
    line-height: 24px !important;

    @include media-breakpoint-down(md) {
        font-size: 13px !important;
        line-height: 16px !important;
    }
}

.fnt-15 {
    font-size: 14px !important;
    line-height: 24px !important;

    @include media-breakpoint-down(md) {
        font-size: 13px !important;
        line-height: 22px !important;
    }
}

.fnt-16 {
    font-size: 16px;
    line-height: 24px;

    @include media-breakpoint-down(md) {
        font-size: 12px;
        line-height: 15px;
    }

}

.fnt-o-16 {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: rgba($color: $white, $alpha: .7);

    @include media-breakpoint-down(md) {
        font-size: 15px;
        line-height: 26px;
    }
}

.fnt-18 {
    font-size: 16px;
    line-height: 26px;

    @include media-breakpoint-up(md) {
        font-size: 18px;
        line-height: 36px;
    }
}

.fnt-20 {
    font-size: 20px;
    line-height: 25px;

    @include media-breakpoint-down(md) {
        font-size: 16px;
        line-height: 20px;
    }
}

.fnt-24 {
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;


    @include media-breakpoint-down(md) {
        font-size: 16px;
        line-height: 20px;
    }
}

.fnt-46 {
    font-weight: 500;
    font-size: 46px;
    line-height: 78px;

    @include media-breakpoint-down(lg) {
        font-size: 36px;
        line-height: 42px;

    }

    @include media-breakpoint-down(md) {
        font-size: 22px;
        line-height: 32px;

    }
}

.fnt-48 {
    font-weight: 600;
    font-size: 48px;
    line-height: 62px;

    @include media-breakpoint-down(lg) {
        font-size: 38px;
        line-height: 44px;

    }

    @include media-breakpoint-down(md) {
        font-size: 28px;
        line-height: 34px;

    }
    @include media-breakpoint-down(sm) {
        font-size: 24px;
        line-height: 32px;

    }
}

.fnt-60 {
    font-weight: 600;
    font-size: 60px;
    line-height: 60px;
    color: #111317;
}

.caps-h {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.16em;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.7;
}

//spacing

.mb-40 {
    margin-bottom: 40px;

    @include media-breakpoint-down(md) {
        margin-bottom: 16px;
    }
}

.mb-32 {
    margin-bottom: 32px;

    @include media-breakpoint-down(md) {
        margin-bottom: 16px;
    }
}


//opacity

.o-6 {
    opacity: .6;
}

.o-7 {
    opacity: .7;
}

.o-8 {
    opacity: .8;
}

.mb-center {
    @include media-breakpoint-down(md) {
        justify-content: center !important;
    }
}

.max-456 {
    max-width: 456px;
}

.max-342 {
    max-width: 342px;

    @include media-breakpoint-down(md) {
        max-width: 100%;
    }
}

.main-p {
    padding-top: 80px;

    @include media-breakpoint-down(xl) {
        padding-top: 57px;
    }

    @include media-breakpoint-down(md) {
        padding-top: 57px;
    }
}

//buttons

.bnt-lg {
    margin-top: 50px;
    width: 100%;
    display: flex;
    background: transparent;

    span {
        display: inline-block;

        &:first-child {
            width: 100%;

            padding: 12px;
            text-align: center;
            background: #AB1A39;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;
        }

        &:last-child {
            background-color: $black;
            width: 48px;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

    }

    border: none;
}

.btn-brown {
    padding: 12px 48px;
    @extend .clr-ab;
    @extend .fnt-500;
    @extend .fnt-16;
    background: #FFFFFF;
    border: 1px solid #AB1A39;
    color: #AB1A39;
    text-decoration: none;
    display: inline-block;

    &:hover {
        background-color: #AB1A39;
        color: #fff;
    }
}

.sec-p {
    padding: 75px 0;

    @include media-breakpoint-down(xl) {
        padding: 48px 0;
    }

    @include media-breakpoint-down(md) {
        padding: 30px 0;
    }


}

.lh-32 {
    line-height: 32px;

    @include media-breakpoint-down(md) {
        line-height: 22px;
    }
}

