.txt-gradiant {
    background: linear-gradient(90deg, #DE4E45 -30.42%, #FF9B95 99.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.btn-m-w100 {
    @include media-breakpoint-down(md) {
        width: 100%;
    }
}

.contact-form {
    &__form {
        // margin-bottom: 48px;

        .form-group {
            margin-bottom: 40px;
            display: flow-root;

            @include media-breakpoint-down(md) {
                margin-bottom: 24px;
            }
        }

        .form-control {
            background: #1F2024;
            border-radius: 8px;
            padding: 16px;
            border: none;

            &::placeholder {
                font-weight: 400;
                font-size: 13px;
                line-height: 24px;


                color: #FFFFFF;

                opacity: 0.6;

                @include media-breakpoint-up(lg) {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 24px;
                    color: #FFFFFF;
                    opacity: 0.6;
                }
            }
        }

        label {
            font-weight: 500;
            font-size: 15px;
            line-height: 24px;
            text-transform: capitalize;
            color: #FFFFFF;
            margin-bottom: 6px;

            @include media-breakpoint-down(md) {
                font-weight: 500;
                font-size: 13px;
                line-height: 24px;
            }
        }
    }

    .nice-select {
        width: 100%;
        background: #1F2024;
        border-radius: 8px;
        border: none;
        height: auto;
        padding: 16px;
        line-height: 0;

        &::after {
            width: 10px;
            height: 10px;
            border-color: $white;
        }

        .current {
            font-weight: 400;
            font-size: 15px;
            line-height: 24px;
            color: #FFFFFF;
            opacity: 0.6;
            font-family: $popin;
        }

        .list {
            width: 100%;
        }
    }
}

.custom-modal {
    .modal-dialog {
        max-width: 732px;
        width: 100%;
    }

    .modal-content {
        background: #1C1D22;
        border-radius: 16px;
        padding: 80px;
    }

    .modal-body {
        text-align: center;
        max-width: 400px;
        margin: auto;

        h2 {
            margin-top: 34px;
            margin-bottom: 8px;
            font-family: $fnt-sora;
            text-transform: capitalize;
        }

        p {
            font-family: $fnt-sora;
            margin-bottom: 40px;
        }

        button {
            width: 173px;

        }
    }
}