.homeBanner {
    // height: 100vh;
    background-image: url("../img/home/BG.png");
    background-repeat: no-repeat;
    background-size: 100%;


    &__svg {
        display: flex;
        justify-content: center;
        // padding-top: 120px;
        padding-top: 36px;
        @include media-breakpoint-down(lg) {
            // width: 100%;
            padding-top: 60px;
        }

        img {
            @include media-breakpoint-down(xxlg) {
                width: 300px;
            }

            @include media-breakpoint-down(lg) {
                width: 100%;
                height: 100%;

            }
        }
    }

    &__text {
        margin-bottom: 40px;

        @include media-breakpoint-down(xxlg) {
            margin-bottom: 24px;
        }

        @include media-breakpoint-down(md) {
            // margin-bottom: 32px;
            margin-bottom: 16px;

        }

        p {
            opacity: .7;
            color: $white;
            text-align: center;

            @include media-breakpoint-down(md) {
                font-size: 13px;
                line-height: 18px;
            }
        }
    }

    &__heading {
        padding: 48px 0;
        position: relative;

        @include media-breakpoint-down(xxlg) {
            padding: 16px 0;
        }

        @include media-breakpoint-down(md) {
           padding-top: 6px;
        }

        h1 {
            font-weight: 600;
            font-size: 48px;
            line-height: 60px;
            color: #FFFFFF;
            text-align: center;

            @include media-breakpoint-down(xxlg) {
                font-size: 38px;
                line-height: 48px;
            }

            @include media-breakpoint-down(md) {
                font-size: 25px;
                line-height: 48px;
            }
        }

        &__img {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            z-index: -1;
            width: 100%;

            @include media-breakpoint-down(lg) {
                width: 100%;
            }
        }
    }
}


