}

//our story
.story {
    &__wrp {
        background: #1C1D22;
        border-radius: 30px;

        // padding: 37px 0 48px 57px;
        h2 {
            font-weight: 600;
            font-size: 40px;
            line-height: 50px;
            background: linear-gradient(90deg, #DE4E45 -30.42%, #FF9B95 99.96%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;

