//approch

.approch {
    &__logo {

        @include media-breakpoint-down(lg) {
            height: 40px;
        }
    }

    &__list {
        margin-left: 68px;

        @include media-breakpoint-down(lg) {
            // margin-top: 52px;
            margin-top: 16px;
            margin-left: 36px;
        }
        @include media-breakpoint-down(md) {
            // margin-top: 52px;
            margin-top: 8px;
            margin-left: 28px;
        }


        &__item {
            display: flex;

            &:not(:last-child) {
                padding-bottom: 80px;

                @include media-breakpoint-down(xxlg) {
                    padding-bottom: 28px;
                }

                @include media-breakpoint-down(lg) {
                    // padding-bottom: 48px;
                    padding-bottom: 12px;
                }

            }

            position: relative;

            &::before {
                content: "";
                position: absolute;
                left: -36px;
                background-image: url("../img/icons/circle.svg");
                width: 16px;
                height: 16px;
                top: 4px;
                z-index: 99;
                @include media-breakpoint-down(md) {
                    left: -28px;
                }
            }

            &:not(:last-child) {
                &::after {
                    content: '';
                    position: absolute;
                    left: -29px;
                    width: 2px;
                    top: 4px;
                    height: 100%;
                    background-color: #292A2F;
                @include media-breakpoint-down(md) {
                    left: -21px;
                }

                }
            }

            &__count {
                margin-right: 58px;

                @include media-breakpoint-down(lg) {
                    margin-right: 26px;
                }
                @include media-breakpoint-down(md) {
                    margin-right: 16px;
                }

                p {
                    @include media-breakpoint-down(lg) {
                        width: 26px;
                    }

                }
            }

            &__content {
                max-width: 438px;

                p {
                    font-family: $popin;
                }
            }
        }
    }

}



