//design

.Design {
    padding: 200px 0 174px;
    position: relative;

    @include media-breakpoint-down(xxlg) {
        padding: 0;
    }

    @include media-breakpoint-down(lg) {
        overflow: hidden;
        padding: 64px 0;
    }

    &__topLeft {
        position: absolute;
        left: 0;
        top: 6%;

        @include media-breakpoint-down(lg) {
            width: 48px;
        }

    }

    &__topRight {
        position: absolute;
        right: 5%;
        top: 20%;

        @include media-breakpoint-down(lg) {
            right: -3%;
            top: 5%;
            width: 30px;
        }
    }

    &__bottomRight {
        position: absolute;
        right: 0;
        bottom: 6%;

        @include media-breakpoint-down(lg) {
            width: 30px;
        }
    }

    &__bottomLeft {
        position: absolute;
        left: 0;
        bottom: 30%;

        @include media-breakpoint-down(lg) {
            width: 20px;
        }
    }

    &__explore {
        padding: 54px 0 70px;
        text-align: center;
        position: relative;

        @include media-breakpoint-down(xxlg) {
            padding: 18px 0 14px;

        }

        @include media-breakpoint-down(lg) {
            // padding: 40px 0 70px;
            padding: 12px 0 18px;


        }

        &__logo {
            @include media-breakpoint-down(xxlg) {
                height: 120px;
            }

            @include media-breakpoint-down(lg) {
                height: 40px;
            }
        }

        .patches__left {
            position: absolute;
            left: 0;
            top: 10%;
            opacity: 0.3;

            @include media-breakpoint-down(lg) {
                width: 24px;
                left: 10%;
                top: 40%;
            }
        }

        .patches__right {
            position: absolute;
            right: 0;
            top: 36%;

            @include media-breakpoint-down(lg) {
                top: 70%;
                width: 24.19px;
            }
        }

    }

    &__card {
        text-align: center;
        @include media-breakpoint-up(lg) {
            margin: 0 32px;

        }
        &:not(:last-child) {
            @include media-breakpoint-down(lg) {
                margin-bottom: 52px;
            }

        }

        &__logo {
            height: 64px;
            width: 64px;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            margin: auto;

            @include media-breakpoint-down(xxlg) {
                height: 45px;
                width: 45px;
            }

            @include media-breakpoint-down(lg) {
                height: 30px;
                width: 30px;
            }

            img {
                @include media-breakpoint-down(xxlg) {
                    width: 100%;
                    height: 100%;
                }
            }
        }

        &__head {
            margin: 30px 0 16px;

            @include media-breakpoint-down(xxlg) {
                margin: 16px 0 8px;
            }

            @include media-breakpoint-down(xxlg) {
                margin: 8px 0 8px;
            }
        }
    }
}


