//represantive
.js-rep-Slider {

    @include media-breakpoint-down(lg) {
        margin-top: 32px;
    }
    @include media-breakpoint-down(lg) {
        margin-top: 20px;
    }
}

.representative {
    // padding: 165px 0 72px;
    position: relative;

    @include media-breakpoint-down(lg) {
        padding: 0px 0 48px;
    }

    .slider-arrow-wrp {
        @include media-breakpoint-down(xxlg) {
            margin-top: 16px;
        }
    }

    &__patch {
        position: absolute;
        background: #1C1D22;
        right: 0;
        bottom: 0;
        width: 80%;
        height: 77%;
        z-index: -1;

        @include media-breakpoint-down(lg) {
            display: none;
        }
    }

    &__slider {
        position: relative;

        &__img {
            border-radius: 12px;
            position: relative;
            overflow: hidden;

            @include media-breakpoint-down(xxlg) {
                height: 470px;
            }
            @include media-breakpoint-down(lg) {
                height: 400px;
            }
            @include media-breakpoint-down(md) {
                height: 320px;
            }

            img {
                width: 100%;

            }

            @include media-breakpoint-down(lg) {
                border-radius: 0;
            }

            &::after {
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg, rgba(20, 21, 24, 0) 0%, rgba(20, 21, 24, 0.9) 100%);
            }

            // width: 456px;
            // height: 565px;
            img {

                // width: 100%;
                // height: 100%;
                @include media-breakpoint-down(lg) {
                    width: 100%;
                    height: 447px;
                    object-fit: cover;
                }
            }
        }

        &__text {
            position: absolute;
            left: 32px;
            right: 32px;
            bottom: 32px;

            @include media-breakpoint-down(lg) {
                left: 16px;
                right: 16px;
                bottom: 20%;
            }

            &__sub {
                font-weight: 600;
                font-size: 18px;
                line-height: 24px;
                color: #FFFFFF;
                font-family: $fnt-sora;
            }

            &__date {
                font-weight: 400;
                font-size: 16px;
                line-height: 20px;
                color: #FFFFFF;
                opacity: 0.8;

                @include media-breakpoint-down(lg) {
                    font-size: 13px;
                    line-height: 16px;
                }
            }
        }
    }
}


.slider-arrow-wrp {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 48px;

    .slider-arrow {
        width: 73px;
        height: 73px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1.5px solid #FFFFFF;

        &.js-arrow-left {

            svg {
                transform: rotate(180deg);
            }
        }
    }

    .slider-count {
        margin: 0 23px;
    }

    .swiper-button-disabled {
        opacity: .5;
        transition: $transition;
    }
}


.blog {
    margin-top: 7rem;

    @include media-breakpoint-down(xxlg) {
        margin-top: 2rem;
    }

    @include media-breakpoint-down(lg) {
        margin-top: -50px;
    }

