            flex-direction: column;
            align-items: flex-start;
        }


        &:not(:last-child) {
            margin-bottom: 58px;

            @include media-breakpoint-down(xxlg) {
                margin-bottom: 20px;
            }

            @include media-breakpoint-down(lg) {
                margin-bottom: 0;
            }

        }

        &__img {

            border-radius: 8px;
            overflow: hidden;
            display: contents;

            @include media-breakpoint-down(lg) {
                width: 100%;
            }

            img {
                width: 216px;
                height: 121px;

                @include media-breakpoint-down(lg) {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        &__body {
            margin-left: 24px;

            @include media-breakpoint-down(lg) {
                margin-top: 12px;
                margin-left: 0;
            }

            p {
                @include media-breakpoint-down(lg) {
                    font-size: 12px;
                    line-height: 18px;
                }
            }

        }
    }
}

.fp-tableCell {
    display: flex;
    align-items: center;
}