.our-banner {
    .homeBanner__heading {
        @include media-breakpoint-down(lg) {
            padding-bottom: 0;
            margin-top: 0;
        }
    }
}


.exits-count {
    display: flex;
    margin-top: 80px;

    @include media-breakpoint-down(lg) {
        margin-top: 40px;
        flex-direction: column;

    }

    &__item {
        width: calc(100%/3);
        text-align: center;

        @include media-breakpoint-down(lg) {
            width: 100%;
        }

        &:not(:last-child) {
            @include media-breakpoint-down(lg) {
                margin-bottom: 24px;
            }
        }

        // backdrop-filter: blur(24px);

        &:nth-child(2) {
            border: 2px solid transparent;
            border-image: url("./img/icons/border-verticel.png") 30 stretch;
        }

        p {
            font-size: 32px;
            line-height: 40px;
            font-weight: 600;
        }
    }


}

.howDow {
    .howDow__head {
        font-weight: 600;
        font-size: 56px;
        line-height: 71px;
        color: #FFFFFF;
        width: 238px;

        @include media-breakpoint-down(md) {
            font-weight: 600;
            font-size: 28px;
            line-height: 34px;
            width: 100%;
            text-align: center;
            margin-bottom: 24px;
        }
    }

    &__card {
        position: relative;

        &:not(:first-child) {
            @include media-breakpoint-down(md) {
                margin-top: 56px;
            }
        }

        &:nth-child(odd) {
            &::before {
                content: "";
                position: absolute;
                right: 0;
                top: 0;
                width: 1px;
                height: 100%;
                background-image: url("../img/ourStory/verticle-line.png");

                @include media-breakpoint-down(md) {
                    display: none;
                }
            }


        }

        &:nth-child(2),
        &:nth-child(3) {
            &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 0;
                width: 100%;
                height: 1px;
                background-image: url("../img/ourStory/horizontal-line.png");

                @include media-breakpoint-down(md) {
                    display: none;
                }
            }


        }

        &:nth-child(3) {
            &::before {
                background-position: bottom;
            }



        }

        &:nth-child(2) {

            &::after {
                top: inherit;
                bottom: 0;
                background-repeat: no-repeat;
                background-position: right;
            }


        }

        &:nth-child(1) {
            position: relative;

            &::after {
                content: "";
                right: 0;
                bottom: 0;
                background-image: url("../img/ourStory/Ellipse 3.png");
                width: 304px;
                height: 317px;
                position: absolute;
            }

            // @include media-breakpoint-up(lg) {
            //     width: 304px;
            //     height: 317px;
            // }
        }

        &:nth-child(2) {
            @include media-breakpoint-up(md) {
                padding-left: 40px;
                padding-bottom: 60px;
            }

            @include media-breakpoint-up(lg) {

                padding-left: 80px;
                padding-bottom: 100px;
            }

        }

        &:nth-child(3) {
            @include media-breakpoint-up(md) {
                padding-right: 40px;
                padding-top: 60px;
            }

            @include media-breakpoint-up(lg) {

                padding-top: 100px;
                padding-right: 80px;
            }
        }

        &:nth-child(4) {
            @include media-breakpoint-up(md) {
                padding-left: 40px;
                padding-top: 60px;
            }

            @include media-breakpoint-up(lg) {

                padding-top: 100px;
                padding-left: 80px;
            }
        }

        &__illustration {

            @include media-breakpoint-up(md) {
                position: absolute;
                bottom: 0;
                right: 20px;
                width: 250px;
            }

            @include media-breakpoint-up(xl) {
                width: auto;
            }
        }

        &__wrp {}

        &__img {
            margin-bottom: 28px;
            @include media-breakpoint-up(xl) {
                height: 74px;
            }
        }
    }
}

.join {
    padding-bottom: 140px;

    @include media-breakpoint-down(md) {
        padding-bottom: 48px;
    }

    &__wrp {
        padding: 56px 64px;
        background: #1C1D22;
        border-radius: 30px;

        @include media-breakpoint-down(md) {
            padding: 40px 24px;
        }
    }

    .dropus {
        @include media-breakpoint-down(md) {
            margin-top: 58px;
        }
    }
}