.fnt-28 {
    font-size  : 24px;
    line-height: 34px;

    @include media-breakpoint-up(lg) {
        font-size  : 28px;
        line-height: 42px;
    }
}

.fnt-32 {
    font-weight   : 600;
    font-size     : 24px;
    line-height   : 32px;
    letter-spacing: -0.04em;

    @include media-breakpoint-up(lg) {
        font-size  : 32px;
        line-height: 44px;
    }
}

.fnt-18-resp {
    @include media-breakpoint-down(lg) {
        font-size  : 18px;
        line-height: 25px;
    }
}

.fnt-14-resp {
    @include media-breakpoint-down(lg) {
        font-size  : 14px;
        line-height: 21px;
    }
}

.fnt-16-resp {
    @include media-breakpoint-down(lg) {
        font-size  : 16px;
        line-height: 24px;
    }
}