}

.header-unpin .navbar {
    // @include media-breakpoint-up(md) {
    transform: translateY(-110px);
    // }
}

.header-pin .navbar {
    transform: translateY(0);
}

.navbar-overide {
    padding: {
        top: rem(15px);
        bottom: rem(10px);
    }

    @include media-breakpoint-down(md) {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    @include media-breakpoint-up(md) {
        padding: {
            top: rem(32px);
            bottom: rem(10px);
        }
    }

    @include media-breakpoint-up(xxlg) {
        padding: {
            top: rem(25px);
            bottom: rem(20px);
        }
    }
}

.navbar-nav {
    align-items: center;
}



.nav-link {
    font-family: $fnt-sora  !important;
    font-weight: 400;
    color: #1A1A1A;
    display: inline-flex;
    padding-right: 0 !important;
    position: relative;

    &:hover {
        background-color: transparent !important;
    }

    @include media-breakpoint-up(md) {
        font-size: 14px;
        line-height: 18px;
    }

    // @include media-breakpoint-up(lg) {
    //     font-size: 18px;
    //     line-height: 26px;
    // }
    margin: {
        left: rem(12px) !important;
        right: rem(12px) !important;
    }

    @include media-breakpoint-up(xxl) {
        margin: {
            left: rem(16px) !important;
            right: rem(16px) !important;
        }
    }

    padding: {
        top: 0 !important;
        bottom: 0 !important;
        left: 0 !important;
        right: 0;
    }

    &::after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: -0.2rem;
        // width: 100%;
        height: 2px;
        // background-color: #AB1A39;
        transition: $transition;
        transform-origin: center;
        transform: translateX(-50%);
    }

    // &:hover::after,
    &.active {

        // transform: scaleX(1) translateX(50%) !important;
        &::after {
            transition: $transition;
            transition-property: transform;
            transform-origin: center;
            transform: translateX(-50%) !important;
        }
    }
}





// Header widget sticky onscrolll
.em-widgetsticky-wrap {
    min-height: 100px;

    &__info {
        display: flex;
        align-items: center;
        width: 90%;

        // expaning
        &.expanded {
            .em-header-widgetpanel {
                display: none;
                transition: all 0.5s ease;
            }

            .em-header-widgetnav {
                display: flex;
                transition: all 0.5s ease;
            }
        }
    }
}

.stax-hamberg {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    text-decoration: none;

    &.menu-toggle-close {
        .nav-toggle {
            span:first-child {
                transition: .5s ease all;
                transform: rotate(45deg) translate(1px, 14px);

                @include media-breakpoint-down(md) {
                    transform: rotate(45deg) translate(0px, 12px);

                }
            }

            span:last-child {
                transition: .5s ease all;
                transform: rotate(-45deg) translate(1px, -12px);

                @include media-breakpoint-down(md) {
                    transform: rotate(-45deg) translate(-1px, -12px);


                }
            }
        }
    }

    &__text {
        font-weight: 700;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 0.16em;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-right: 23px;

        @include media-breakpoint-down(md) {
            font-size: 10px;
            line-height: 13px;
            margin-right: 16px;
        }
    }

    .nav-toggle {
        user-select: none;
        cursor: pointer;
        height: auto;
        // left: 0;
        position: relative;
        width: 40px;
        z-index: 2;
        display: table;

        span {
            width: 32px;
            border: 2px solid #FFFFFF;
            border-radius: 4px;
            display: block;
            transition: .5s ease all;

            @include media-breakpoint-down(md) {
                width: 27px;
            }

            &:first-child {
                margin-bottom: 15px;

                @include media-breakpoint-down(md) {
                    margin-bottom: 12px;
                }
            }
        }
    }
}

