.navbar-collapse {
    @include media-breakpoint-down(lg) {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100vh;
        bottom: 0;
        background: #0D131F;
        z-index: 10;
        transform: translateX(-100%);
        transition: $transition;
        overflow-y: auto;

        &.open-hamberg {
            transform: translateX(0%);

            .nav-link {
                color: $w  !important;
            }
        }

        .navbar-list {
            padding-top: 6rem !important;



        }

        .nav-item {
            margin-bottom: 1.2rem !important;
        }


        .nav-link {
            border-bottom: 1px solid rgba($color: #192954, $alpha: 0.1);
            font-size: 18px !important;
            line-height: 28px !important;

            padding: {
                left: 1rem !important;
                right: 1rem !important;
                bottom: 1.2rem !important;
            }

            @include media-breakpoint-down(lg) {
                text-align: left;
            }

            // text-transform: capitalize;
            display: block;

            &::after {
                display: none;
            }
        }
    }
}

.un-mobile-hamberg {
    &.active {
        .nav-toggle {
            display: none;
        }
    }
}

