// hamberger menu design 

.menu-toggle {
    position: absolute;
    top: 2rem;
    right: 2rem;
    border: none;
    background-color: transparent;
    color: inherit;
    font-size: 1.625rem;
    cursor: pointer;
}

.menu {
    position: absolute;
    inset: 0;
    background: linear-gradient(93.32deg, #DE4E45 -8.92%, #FF9B95 372.77%);
    color: #f1faee;
    height: 100vh;
    transform: translateY(-100vh);
    overflow-y: hidden;
    display: flex;

    // align-items: center;
    &__wrpContainer {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .menu-wrp {
        flex: 1;
    }

    &__footer {
        display: flex;
        justify-content: space-between;

        &__social {
            @include media-breakpoint-down(md) {
                text-align: center;
            }

            &__text {
                font-weight: 700;
                font-size: 12px;
                line-height: 15px;
                letter-spacing: 0.16em;
                text-transform: uppercase;
                color: #FFFFFF;
                opacity: 0.7;

            }

            .social-icons {
                display: flex;
                align-items: center;
                margin-top: 15px;

                @include media-breakpoint-down(md) {
                    margin-top: 8px;
                    justify-content: center;
                }

                &__items {
                    &:not(:last-child){
                        margin-right: 32px;

                    }

                    img {
                        transition: .5s ease transform;

                        &:hover {
                            transform: scale(1.1);
                            transition: .5s ease transform;
                        }
                    }
                }
            }
        }
    }
}

.menu a {
    font-weight: 600;
    font-size: 40px;
    line-height: 50px;
    text-transform: capitalize;
    color: #FFFFFF;
    text-decoration: none;

    @include media-breakpoint-down(lg) {
        font-size: 28px;

    }
}

.menu a:hover {
    // color: #a8dadc;
}

.menu-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin-inline: auto;
    // padding-block: 2rem;
}

.menu nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // flex-grow: 1;
    // gap: 1rem;


}

.js-portfolio-imgSlider {
    .swiper-slide {
        opacity: 0 !important;

        &.swiper-slide-active {
            opacity: 1 !important;
        }
    }
}

.menu nav a {
    position: relative;
    margin-bottom: 16px;
    width: 100%;
    text-align: center;

    @include media-breakpoint-up(lg) {
        margin-bottom: 30px;

    }

    @include media-breakpoint-up(xxl) {
        margin-bottom: 56px;

    }

    .sub-text {
        transition: .7s ease transform;
        transition-delay: 3s;
        transform: translateX(-50%, 50px);
        display: none;
        transition: .5s ease transform;
        position: absolute;
        bottom: 0;
        left: 50%;
        font-size: 16px;
        @extend .fnt-400;


    }

    &:hover {
        .sub-text {
            display: block;
            transition: .7s ease transform;
            transition-delay: 3s;
            transform: translate(-50%, 40px);

        }


    }
}

.menu nav a::before {
    content: attr(data-title);
    position: absolute;
    top: 41%;
    left: 50%;
    width: 100%;
    // inset-inline: -0.5rem;
    // height: 6px;
    // background-color: currentColor;
    font-size: 90px;
    opacity: 0;
    transform: translate(-50%, -50%);

    transition: opacity 0.5s ease-out;

    @include media-breakpoint-down(lg) {
        font-size: 38px;
    }

    @include media-breakpoint-down(md) {
        display: none;
    }
}

.menu nav a:hover::before {
    transition: opacity 0.5s ease-out;
    opacity: 8%;
}