.stackFooter {
    padding: 120px 0;
    // background-color: $bg-gradient-de;
    background: linear-gradient(93.32deg, #DE4E45 -8.92%, #FF9B95 372.77%);

    @include media-breakpoint-down(lg) {
        padding: 40px 0;
    }

    &__bottomContent {
        margin-top: 68px;

        @include media-breakpoint-down(lg) {
            margin-top: 40px;
        }

        &__wrp {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }

    .subscribe-form {
        margin-top: 12px;

        // display: flex;
        .search-input {
            padding: 12px 16px;
            background: #ED7A74;
            border-radius: 8px;
            max-width: 336px;
            border: none;

            @include media-breakpoint-down(md) {
                max-width: 100%;
            }

            &::placeholder {
                font-weight: 500;
                font-size: 15px;
                line-height: 24px;
                color: #FFFFFF;
                opacity: 0.6;
                font-family: $popin;
            }

            &:focus {
                box-shadow: none;
            }
        }
    }

    .footer-condition-links {
        display: flex;
        margin-top: 44px;

        @include media-breakpoint-down(lg) {
            margin-top: 30px;
        }

        li:first-child {
            margin-right: 48px;
        }
    }

    .footer-links {
        @extend .fnt-16;
        font-weight: 600;
        color: $white;
        text-decoration: none;
        transition: $transition;

        &:hover {
            transition: $transition;
            color: $black;
        }
    }

    .footer-nav-links {
        column-count: 2;
        column-gap: 40px;
        @include media-breakpoint-down(lg) {
            column-gap: 8px;
            column-count: 3;
            margin-top: 32px;

        }

        li {
            margin-bottom: 40px;

            @include media-breakpoint-down(md) {
                margin-bottom: 32px;
            }
        }
    }
}