// LP OUR SERVICES - VIDEO
.zw-play-btn {
  position     : absolute;
  top          : 50%;
  left         : 50%;
  height       : rem(60px);
  width        : rem(60px);
  border       : 4px solid $white;
  cursor       : pointer;
  border-radius: 50%;
  background   : $bg-pri;
  transform    : translate(-50%, -50%);
  z-index      : 10;

  @include media-breakpoint-up(md) {
    height: rem(80px);
    width : rem(80px);
  }
}

.zw-play-icon {
  position    : relative;
  width       : 0;
  height      : 0;
  border-style: solid;
  left        : 2px;
  border-width: 0.5em 0 0.5em 0.8em;
  border-color: transparent transparent transparent $primary;

  @include media-breakpoint-up(md) {
    border-width: 0.8em 0 0.8em 1.2em;
  }
}