.btn {
    padding: 22px 45px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    border-radius: 17px;

    @include media-breakpoint-down(lg) {
        font-size: 14px;
        line-height: 18px;
        padding: 18px 32px;
    }

    &--sm {
        padding: 16px 20px;
        font-weight: 400;
        font-size: 15px;
        line-height: 19px;
        color: #FFFFFF;
    }

    &--black {
        color: $white;
        background: #000000;
        border-radius: 8px;
        border-color: $black;
        transition: $transition ease all;

        &:hover {
            color: $black;
            background-color: $transparent;
            transition: $transition ease all;

        }
    }

    &--rose {
        color: $white;
        background: #DE4E45;
        border: 1px solid #DE4E45;
        transition: $transition ease all;

        &:hover {
            color: #DE4E45;
            background-color: transparent;
            transition: $transition ease all;
        }
    }

    &--blue {
        background: linear-gradient(93.8deg, #44D4D4 -58.89%, #1A6A6A 100%);
        color: $white;

        &:hover {
            color: $white;
        }

        svg {
            svg {
                path {
                    stroke: $white;

                }
            }

            &:hover {
                color: $white;
            }

        }
    }

    &:is(:hover, :focus) {
        // color: $white;

        svg {
            transform: translateX(10px);
            transition: $transition;
        }
    }

    svg {
        margin-left: 10px;
        transition: $transition;
    }
}

//link arrow


.link-arrow {
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: #FFFFFF;
    text-decoration: none;

    @include media-breakpoint-down(lg) {
        font-size: 15px;
        line-height: 19px;
    }

    &:is(:hover, :focus) {
        color: $white;

        svg {
            transform: translateX(10px);
            transition: $transition;
        }
    }

    svg {
        margin-left: 10px;
        transition: $transition;
    }

    &--yellow {
        color: #F6DE4A;

        svg {
            path {
                stroke: #F6DE4A;

            }
        }

        &:hover {
            color: #F6DE4A;
        }
    }
}