.mr-navtabs-overide {
  justify-content: space-between;

  .nav-link {
    position      : relative;
    font-size     : 20px;
    line-height   : 30px;
    border        : none;
    // margin        : 0 !important;
    padding-bottom: .8rem !important;
    font-weight   : 500;
    @include media-breakpoint-down(md){
      font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    }

    .mr-navtabs-overide {
      @include media-breakpoint-down(md){
        border: 0 !important;
      }
    }

    &::after {
      content         : "";
      position        : absolute;
      bottom          : 0;
      left            : 0;
      height          : 3px;
      width           : 100% !important;
      transform       : scaleX(0);
      transition      : all 600ms ease 0s;
      transform-origin: left;
      background-color: #AB1A39;
    }

    &.active {
      &::after {
        transform: scaleX(1);
      }
    }
  }
}
