.zw-banner {
    overflow: hidden;
    &__content {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        width: 100%;
        margin: 0 auto;
        color: $bg-pri;
        transform: translateY(-50%);
        display: flex;
        flex-direction: column;
        z-index: 10;
        @include media-breakpoint-down(md) {
            padding: {
                left: 3rem;
                right: 3rem;
            }
        }
        &-top {
            top: inherit;
            transform: translate(0);
            @include media-breakpoint-down(md) {
                bottom: 0;
            }
            @include media-breakpoint-up(md) {
                top: 0;
            }
        }
    }
    &__heading {
        // font-family: $fnt-r;
        position: relative;
    }
    &__des {
        color: rgba($color: #f9ffec, $alpha: 0.9);
        padding: {
            top: rem(30px);
            bottom: rem(30px);
        }
        @include media-breakpoint-down(lg) {
            padding: {
                left: rem(15px);
                right: rem(15px);
            }
        }
        @include media-breakpoint-up(lg) {
            padding: {
                top: rem(72px);
                bottom: rem(72px);
            }
        }
    }
    // Water irrigation
    &__overlay-textinfo {
        @include media-breakpoint-up(lg) {
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
        }
    }
}

.imgh310 {
    height: 200px;
    @include media-breakpoint-up(md) {
        height: 310px;
    }
}

.zw-max1100 {
    margin: 0 auto;
    @include media-breakpoint-up(md) {
        max-width: 800px;
    }
    @include media-breakpoint-up(xl) {
        max-width: 1000px;
    }
    @include media-breakpoint-up(xxlg) {
        max-width: 1100px;
    }
}