.mr-card {
    &__figwrap {
        position: relative;
    }

    &__body {
        padding-top: 1.4rem;
    }

    &__title {
        font-weight  : $fnt-600;
        padding-right: 2rem;
        line-height  : 26px !important;
    }

    &__text {
        color      : #425466;
        line-height: 32px !important;
    }
}

.mr-imgx200 {
    height    : 200px;
    // object-fit: cover;
}

.card-img-top {
    border-radius: 0;
}

.mr-card-link {
    @extend .fnt-16;
    color          : $mercury-red;
    font-weight    : $fnt-700;
    text-decoration: none;
    display        : inline-block;
}

.mr-play-btngroup {
    position: absolute;
    top     : 0;
    left    : 0;
    right   : 0;
    bottom  : 0;
}

.mr-play-btn {
    height       : rem(60px);
    width        : rem(60px);
    background   : $white;
    position     : absolute;
    left         : 50%;
    transform    : translate(-50%, -50%);
    top          : 50%;
    border-radius: 50%;
    background   : rgba($color: #AB1A39, $alpha: .4);

    &__circle {
        height       : rem(40px);
        width        : rem(40px);
        background   : $white;
        border-radius: 50%;
    }
}


.mr-overlay {
    position  : absolute;
    top       : 0;
    bottom    : 0;
    left      : 0;
    right     : 0;
    height    : 100%;
    width     : 100%;
    background: rgba($color: #000000, $alpha: .4);
}

// Mobile number slider
.js-mb-ntwrk-slider {
    li {
        border : 1px solid #bebebe;
        padding: 1.2rem 1.5rem !important;
    }
}